@font-face {
  font-family: "Lobster";
  font-weight: 400;
  font-style: normal;
  src: local("Lobster"),
    url("./../../fonts/Lobster-Regular.woff2") format("woff2"),
    url("./../../fonts/Lobster-Regular.woff") format("woff"),
    url("./../../fonts/Lobster-Regular.eot") format("eot");
  font-display: swap;
}
@font-face {
  font-family: "DancingScript";
  font-weight: 400;
  font-style: normal;
  src: local("DancingScript"),
    url("./../../fonts/DancingScript-Medium.woff2") format("woff2"),
    url("./../../fonts/DancingScript-Medium.ttf") format("ttf"),
    url("./../../fonts/DancingScript-Medium.eot") format("eot");
  font-display: swap;
}

.home {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 100px;

  &__inner {
    display: flex;
    flex-direction: column;
    padding: 40px;
    border-radius: 12px;
    margin: 30px;
    background: #ffffff71;
  }

  &__title {
    margin-bottom: 20px;
    width: 600px;
    display: flex;
    align-items: end;
    justify-content: space-between;
  }
  &__header {
    font-size: 40px;
    color: #19535b;
  }

  &__name {
    font-size: 50px;
    color: #63d06ad3;
    align-self: flex-end;
    font-family: Lobster, sans-serif;
  }
  &__junior {
    font-family: DancingScript, sans-serif;
  }
}
